.header {
  width: 100%;
  position: absolute;
  z-index: 20;
  overflow: hidden;
  z-index: 100;
}

@media (max-width: 540px) {
  .header-menu {
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    align-self: center;
  }
}

.header-logo {
  width: 100%;
}

.header .logo-text {
  color: #fff;
}

.header-contact {
  display: flex;
  flex-direction: row;
  gap: 20px;
  cursor: pointer;
  width: 100%;
  justify-content: flex-end;
}

@media (max-width: 768px) {
  .header-contact {
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
    justify-content: flex-start;
  }
}

@media (max-width: 540px) {
  .header-contact {
    margin-bottom: 20px;
    flex-direction: row;
  }
}

.header-contact a {
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 16px;
  z-index: 5000 !important;
  white-space: nowrap;
}

@media (max-width: 540px) {
  .header-contact a {
    font-size: 14px;
  }
}

.header-contact_tel::before {
  content: '';
  width: 30px;
  height: 30px;
  display: block;
  border-radius: 50%;
  background: url(../img/ico-tel.svg) no-repeat center;
  background-size: 18px;
  background-color: #d41a3b;
  margin-right: 15px;
}

@media (max-width: 768px) {
  .header-contact_tel::before {
    width: 20px;
    height: 20px;
    background-size: 12px;
  }
}

.header-contact_email::before {
  content: '';
  width: 20px;
  height: 30px;
  display: block;
  border-radius: 0px;
  background: url(../img/ico-marker-1.svg) no-repeat center;
  background-size: contain;
  margin-right: 15px;
}

@media (max-width: 768px) {
  .header-contact_email::before {
    height: 20px;
  }
}
